// Homepage
@media (min-width: 320px) and (max-width: 330px) {
    body {
        overflow-x: hidden !important;
    }
    nav {
        padding: 12px !important;
        .navbar {
            justify-content: space-between !important;
            min-width: 320px;
        }
        .navbar-brand {
            font-size: 18px;
            padding: 0 10px;
        }
        .navbar-toggler {
            border-radius: 0;
            right: 0;
            span {
                font-size: 15px;
            }
        }
        .nav {
            flex-direction: column;
            .nav-item {
                margin: 20px 0 !important;
                font-size: 17px !important;
                text-align: center !important;
            }
        }
    }
    .about-info {
        display: inline-block;
        padding: 0 !important;
        .side2 {
            text-align: center;
            img {
                width: 170px;
            }
        }
        .side1 {
            text-align: center;
            width: 100%;
            h1 {
                font-size: 27px;
            }
            p {
                font-size: 15px;
                width: 100%;
                padding: 0 15px;
                margin: 15px 0 25px;
            }
            .buttons {
                .linkBtn {
                    padding: 8px 14px;
                    font-size: 14px;
                    font-weight: 400;
                }
            }
            .socials {
                margin-top: 40px;
                a {
                    .iconSvg {
                        padding: 6px;
                        font-size: 35px;
                    }
                    &:last-child {
                        padding-right: 0 !important;
                    }
                }
            }
        }
    }
}

