// Portfolio

@media (min-width: 320px) and (max-width: 330px)  {
    .titles {
        h3 {
            font-size: 30px !important;
        }
        p {
            font-size: 18px !important;
        }
    }
    .row {
        .col-md-12 {
            padding-top: 0;
            .projectCard {
                display: inline-block;
                border: none;
                border-radius: 0 !important;
                .projectImage {
                    width: 100%;
                    border-top-left-radius: 0px;
                    border-top-right-radius: 0;
                }
                .definition  {
                    margin: 30px 0;
                    .projectInfo {
                        text-align: center;
                        h3 {
                            font-size: 20px !important;
                        }
                        p {
                            font-size: 16px !important;
                            width: 100%;
                        }
                    }
                    .projectButtons {
                        text-align: center;
                        .buttons {
                            padding: 8px 12px !important;
                            font-size: 15px;
                            &:nth-child(2) {
                                margin-left: 13px;
                            }
                        }
                    }
                }
            }
            &:nth-child(2) {
                .projectCard {
                    .projectImage {
                        border-top-right-radius: 0;
                    }
                    .definition {
                        margin: 0;
                    }
                }
            }
        }
    }
    .footer {
        p {
            font-size: 13px;
        }
    }
}

