// Fonts
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif !important;
    overflow-x: hidden;
}
// Colors
$homebtnColor: #2b2b2b;
$homebtnColor2: #faff00;
// Navbar
nav {
    width: 100vw;
    display: flex;
    justify-content: space-between !important;
    animation: nav 1s ease;
    padding: 15px 80px !important;
    .navbar-brand {
        font-size: 25px;
        font-family: 'Poppins', sans-serif;
        b {
            font-weight: 800;
        }
    }
    .navbar-collapse {
        right: 0;
        flex-grow: 0;
        .nav-item {
            margin: 20px;
            font-size: 20px;
            a {
                color: #2d2d2d;
                text-decoration: none;
                transition: 0.3s ease;
                -webkit-transition: 0.3s ease;
                font-weight: 500;
                &:hover {
                    color: black;
                    
                }
            }
        }
    }
}
// Nav fixed
.fixed {
    padding: 10px !important;
    position: fixed !important;
    top: 0;
    left: 0;
    z-index: 99;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    color: #fff;
    background-color: rgba(0, 0, 0, 0.1);
}
// AboutInfo
.about-info {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row-reverse;
    padding: 50px 0 0 0;
    .side1 {
        width: 55%;
        margin: 20px 0;
        h1 {
            font-size: 70px;
            font-family: 'Montserrat', sans-serif;
            font-weight: 700;
            animation-delay: 0.8s;
            animation: fadeInup 2s ease;
        }
        p {
            width: 75%;
            color: #2d2d2d;
            margin: 15px 3px 60px;
            font-size: 20px;
            animation: fadeInup 2s ease;
            animation-delay: 0.8s;
        }
        .buttons {
            .linkBtn {
                background-color: $homebtnColor;
                padding: 17px 40px;
                color: #fff;
                font-size: 20px;
                text-decoration: none;
                font-weight: 500;
                border-radius: 30px;
                &:nth-child(2) {
                    background-color: $homebtnColor2;
                    margin-left: 15px;
                    color: #000;
                }
            }
        }
        .socials {
            margin-top: 60px !important;
            margin: 0 auto;
            a {
                padding: 0 30px 0 0;
                text-decoration: none;
               
                .iconSvg {
                    color: #fff;
                    padding: 10px;
                    background-color: #2d2d2d;
                    border-radius: 50%;
                    font-size: 45px;
                    &:hover {
                        background-color: #2d2d2d9c;
                        transform: rotate(360deg);
                        transition: 0.5s ease;
                    }
                }
            }
        }
    }
    .side2 {
        img {
            width: 95%;
            background-image: url('../Home/rm222batch3-mind-03.jpg');
            top: 30px;
            border-radius: 50%;
        }
    }
}
// Mixing
@mixin PageTitle1 {
    font-size: 45px;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    margin: 0;
    letter-spacing: 2px;
    color: #000;
}
@mixin PageTitle2 {
    font-size: 30px;
    color: #2d2d2d;
    font-family: 'Poppins', sans-serif;
    letter-spacing: 0.7px;
}
// portfolio
.titles {
    animation: fadeInup ease 2.3s;
    text-align: center;
    margin-top: 75px;
    .title1{
        @include PageTitle1();
    }
    .title2 {
        @include PageTitle2();
    }
}
.row {
    .col-md-12 {
        padding-top: 50px;
        .projectCard {
            min-height: 300px;
            border-radius: 5px;
            display: flex;
            border-top-left-radius: 60px;
            border-bottom-right-radius: 60px;
            border: 2px solid #000;
            justify-content: space-between;
            margin: 50px 0;
            .projectImage {
                width: 50%;
                border-top-left-radius: 58px;
            }
            .definition {
                margin: 30px 50px;
                .projectInfo {
                    h3 {
                        font-weight: 600;
                    }
                    p {
                        width: 80%;
                        color: #2d2d2d;
                        font-size: 20px;
                        margin: 10px 0 50px;
                    }
                }
                .projectButtons {
                    .buttons {
                        background-color: #2b2b2b;
                        padding: 17px 35px;
                        color: #fff;
                        font-size: 20px;
                        border-radius: 30px;
                        text-decoration: none;
                        font-family: 'Montserrat', sans-serif;
                        font-weight: 500;
                        svg {
                            margin-left: 10px;
                        }
                        &:nth-child(2) {
                            background-color: $homebtnColor2;
                            margin-left: 30px;
                            color: #000;
                        }
                    }
                }
            }
        }
        &:nth-child(2) {
            .projectCard {
                border-radius: 5px;
                border-top-left-radius: 0;
                border-bottom-right-radius: 0;
                border-top-right-radius: 60px;
                border-bottom-left-radius: 60px;
                flex-direction: row-reverse;
                .projectImage {
                    border-top-left-radius: 0;
                    border-top-right-radius: 58px;
                }
                .definition {
                    margin: 30px 0 0 40px;
                    p {
                        width: 80%;
                    }
                }
            }
        }
    }
}
// footer 
.footer {
    text-align: center;
    margin: 20px 0;
    p {
        font-size: 17px;
        font-family: 'Poppins', sans-serif;
        svg {
            color: red;
        }
    }
}
// certificate
.certificationMain {
    margin-bottom: 50px;
    .certImg {
        display: flex;
        justify-content: center;
        img {
            margin-top: 100px;
            width: 50%;
            margin-bottom: 30px;
            border-radius: 10px;
        }
    }
    .myCertificate {
        padding: 10px;
        a {
            text-decoration: none;
            color: #000;
            font-family: 'Montserrat', sans-serif;
            font-weight: bold;
            font-size: 30px;
            display: flex;
            align-items: center;
            letter-spacing: 0.03em;
            svg {
                margin-left: 4px;
                font-weight: 900;
                margin-bottom: 5px;
            }
            &:hover {
                svg {
                    margin-left: 15px;
                    transition: all 0.5s linear;
                }
            }
        }
        p {
            font-family: 'Poppins', sans-serif;
            font-size: 20px;
            font-weight: 400;
            width: 80%;
            line-height: 30px;
        }
    }
}
// about
.aboutmain {
    margin: 90px 0 50px 0;
    .container {
        .row {
            .col-md-6 {
                img {
                    width: 100%;
                    border-radius: 10px;
                }
                p {
                    color: #2d2d2d;
                    font-size: 20px;
                    margin-bottom: 20px;
                }
                h2 {
                    margin-bottom: 10px;
                    font-weight: 700;
                    font-size: 32px;
                }
                &:nth-child(2) {
                    padding-left: 20px;
                    .question {
                        margin-top: 40px;
                    }
                }
            }
        }
    }
}